import React, { Component } from 'react';
import { Table } from 'react-bootstrap';
import { Glyphicon} from 'react-bootstrap';
import { Button, ButtonToolbar } from 'react-bootstrap';
import { AddDepModal } from './AddDepModal';
import { EditDepModal } from './EditDepModal';

export class Department extends Component {

    constructor(props) {
        super(props);
        this.state = { deps: [], addModalShow: false, editModalShow: false }

    }

    refreshList() {

        fetch('api/department')
            .then(response => response.json())
            .then(data => {
                this.setState({ deps: data });
            });
        
    }


    componentDidMount() {
        this.refreshList();
        document.getElementById('EditForm').style.display = 'none';
        document.getElementById('AddForm').style.display = 'none';

        document.getElementById('mainbox').style.display = 'block';
    }
    closeSub() {
        document.getElementById('EditForm').style.display = 'none';
        document.getElementById('AddForm').style.display = 'none';

        document.getElementById('mainbox').style.display = 'block';
    }
    Addsub() {
        document.getElementById('EditForm').style.display = 'none';
        document.getElementById('AddForm').style.display = 'block';

        document.getElementById('mainbox').style.display = 'none';
    }
    componentDidUpdate() {
        this.refreshList();
    }

    handledepartmentnameChange = (event) => {
        this.setState({
            DepartmentName: event.target.value
        })

    }
    handleSubmit = event => {
        var departmentName = document.getElementById("departmentName").value;
        var DepartmentId = document.getElementById("departmentId").value;

        if (departmentName == "" || departmentName == null) {
            alert("Department Name Empty");



        }
        else {


            event.preventDefault()

            fetch('api/department/', {
                
                method: 'Put',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },

                body: JSON.stringify({
                    DepartmentId: event.target.departmentId.value,
                   
                    DepartmentName: departmentName,
               
                })
              
            })
                .then(res => res.json())
                .then((result) => {
                    alert(result);
                    this.componentDidMount();



                },
                    (error) => {
                        alert('Failed');
                    })
        }

    }


    handleAdd = event => {

        if (this.state.DepartmentName == "" || this.state.DepartmentName == null) {
           
            document.getElementById('AdddepartmentName').focus();
          
          
        }
        else {


            event.preventDefault()

            fetch('api/department', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },

                body: JSON.stringify({

                    DepartmentName: this.state.DepartmentName,

                })
            })
                .then(res => res.json())
                .then((result) => {
                    document.getElementById('AdddepartmentName').value = '';
                    alert(result);
                    this.componentDidMount();
                   


                },
                    (error) => {
                        alert('Failed');
                    })
        }
        return false;
       
    }
    deleteDep(depid) {
        if (window.confirm('Are you sure?')) {
            fetch('api/department/' + depid, {
                method: 'DELETE',
                header: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            })
        }
    }
    GetDep(depid) {
       fetch('api/department/' + depid)
            .then(response => response.json())
            .then(data => {
               
               
                data.map(res => {
                    document.getElementById('departmentId').value = res.departmentId;
                    document.getElementById('departmentName').value = res.departmentName;
                    
                });
               
                document.getElementById('EditForm').style.display = 'block';
                document.getElementById('mainbox').style.display = 'none';
                    
            });
       
       
    }
    render() {
        const { deps, depid, depname } = this.state;
       


        return (

            <div >
                <h1> Department</h1>
                <div className="row">
                    <div id="EditForm" class="EditForm">
                    <div>
                            <button class="Eclosebtn" onClick={this.closeSub}> X </button>
                    </div>
                    <form onSubmit={this.handleSubmit}>
                        <div  >
                           
                            <div class="collapse">
                                <input Id="departmentId" type="text" defaultValue={this.state.departmentId} onChange={this.handledepartmentnameChange} />
                                </div>
                                <div class="Eform">
                            <label>
                                Department Name
                            </label>
                                    <input id="departmentName" type="text" defaultValue={this.state.DepartmentName} onChange={this.handledepartmentnameChange} />
                                </div>
                                <br />
                                <ButtonToolbar>
                                    <div class="btnHit" >
                                    <Button class="btn btn-success" type="submit" variant='primary'>
                                    Update Department</Button>
                                        </div>

                            </ButtonToolbar>
                        </div>
                       
                    </form>
                    </div>


                    <div id="AddForm" class="EditForm">
                        <div>
                            <button class="Eclosebtn" onClick={this.closeSub}> X </button>
                        </div>
                        <form onSubmit={this.handleAdd}>
                            <div  >

                                <div class="Eform">
                                    <label>
                                        Department Name
                                    </label>
                                    <input id="AdddepartmentName" type="text" required Value={this.state.DepartmentName} onChange={this.handledepartmentnameChange} />
                                </div>
                                <br />
                                <ButtonToolbar>
                                    <div class="btnHit" >
                                        <Button class="btn btn-success" type="submit" variant='primary'>
                                            Add Department</Button>
                                    </div>

                                </ButtonToolbar>
                            </div>

                        </form>
                    </div>
                </div>
                <div id="mainbox" class="EditForm">
                    <Table class="table table-hover ">
                        <thead>
                            <button id="AddDep" onClick={this.Addsub}>
                                <Glyphicon glyph='glyphicon glyphicon-plus-sign' />Add Dep Department
                            </button>
                        </thead>
                    <thead>
                        <tr>
                            <th>Department ID</th>
                            <th>Department Name</th>
                            <th>Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {deps.map(dep =>
                            <tr key={dep.departmentId}>
                                <td>{dep.departmentId}</td>
                                <td>{dep.departmentName}</td>
                                <td>
                                    
                                        <Button class="primary" variant="danger"
                                            onClick={() => this.GetDep(dep.departmentId)}>
                                            Edit
                                        </Button>

                                    <Button class="danger" variant="danger"
                                            onClick={() => this.deleteDep(dep.departmentId)}>
                                            Delete
                                        </Button>

                                       
                                    

                                </td>

                            </tr>)}
                    </tbody>

                    </Table>
                </div>
                
            </div>
        )
    }
}


