import React, { Component } from "react";
export class AddForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            DepartmentName: ''
        }
    }

    handledepartmentnameChange = (event) => {
        this.setState({
            DepartmentName: event.target.value
        })

    }
    handleSubmit = event => {

        if (this.state.DepartmentName == "" || this.state.DepartmentName == null) {
            alert("Department Name Empty");
            
           
           
        }
        else {

       
        event.preventDefault()
        
        fetch('api/department', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            
            body: JSON.stringify({
           
                DepartmentName: this.state.DepartmentName,

            })
        })
            .then(res => res.json())
            .then((result) => {
                alert(result);

               
               
            },
                (error) => {
                    alert('Failed');
                })
        }

    }
    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <div>

                    <label>
                        DepartMent Name
                    </label>
                    <input id="department" type="text" value={this.state.DepartmentName} onChange={this.handledepartmentnameChange} />
                </div>
                <button type="submit" >Add</button>
            </form>



        )
    }
}



